<template>
<div class="row">
      <div class="col-lg-8" v-if="ready">
        <form id='general_req_form' enctype="multipart/form-data" class="form-horizontal" data-vv-scope="form1"  @submit.prevent="handleUpdate('form1')" >
        <div class="card">
          <div class="card-header">
            <strong>Check List Form</strong>
          </div>            
          <div class="card-body card-block">
              <div class="row form-group">
                <div class="col col-md-3"><label class=" form-control-label">Requester</label>
                </div>
                <div class="col-12 col-md-9">
                  <p class="form-control-static">{{ fetchdata[0].req_name }}</p>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="select" class=" form-control-label">Sub department</label></div>
                <div class="col-12 col-md-9">
                  <input type="text" class="form-control" v-model="req.sub" required readonly>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="select" class=" form-control-label">Item</label></div>
                <div class="col-12 col-md-9">
                  <input type="text" class="form-control" v-model="req.cat" required readonly>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="select" class=" form-control-label">Status</label></div>
                <div class="col-12 col-md-9">
                  <select name="Request_for" id="rf" class="form-control" v-model="req.stat" required>
                        <option v-for="state in status">{{ state }}</option>
                    </select>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Description" class=" form-control-label">Description</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <textarea id ="Description" name="Description" rows="3" cols="80" placeholder="Enter description" maxlength="250" class="form-control" required v-model="req.descript" readonly></textarea>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Location" class=" form-control-label">Location</label></div>
                <div class="col-12 col-md-9">
                    <input type="text" placeholder="Enter Location" maxlength="15"  class="form-control" v-model="req.location" required readonly/>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Due_time" class=" form-control-label" >Action</label></div>
                <div class="col-12 col-md-9">
                   <input type="text" class="form-control" v-model="req.remarks" required/>
                </div>
              </div>    
              <div class="row form-group" style="height:auto" v-if="req.imgstat!='false'">
                <div class="col col-md-3"><label class=" form-control-label" >Supporting Image</label></div>
                <div class="col-12 col-md-9">
                   <button type="button" value="View Image" @click="viewimg" ref="showimg" style="width:8em;height:2.5em;" class="form-control" v-if="notshow">View Image</button>
                     <div v-html="imgbox" style="height:13em"> {{ imgbox }}</div>
                </div>
              </div>         
            
      </div>
        <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm">
              <i class="fa fa-paper-plane"></i> Update
            </button>
            <button type="button" class="btn btn-danger btn-sm" onclick="window.history.go(-1); return false;">
              <i class="fa fa-ban"></i> Cancel
            </button>
        </div>
    </form>
    </div>   
</template>
<script>
export default {
    data(){
        return{
            reqdp:'',
            category:'',
            status:['Not Started','In Progress','Completed'],
            ready:false,
            fetchdata:[],
            req:{
                id:'',
                name:'',
                sub:'',
                cat:'',
                stat:'',
                descript:'',
                location:'',
                remarks:'',
                imgstat:'false',               
            },
            notshow:true
        }
    },
    async created(){
        var id = document.getElementById('tmp_id').value;
        var formData = new FormData();
        formData.append('id', id);
        await axios.post('RequestPortal/backend/fetch_check_record.php',formData)
        .then(
            (response)=>{
                this.fetchdata = response.data;
                this.req.id = response.data[0].id;
                this.fetchdata.req_name = response.data.req_name;
                this.req.sub = response.data[0].sub_dept;
                this.req.cat = response.data[0].item;
                this.req.descript = response.data[0].description;
                this.req.location = response.data[0].location;
                this.req.stat = response.data[0].status;
                this.req.remarks = response.data[0].remarks;
                this.req.imgstat = response.data[0].img_hv;
                this.ready = true;
            }
        )
    },
    async mounted(){
        window.onbeforeunload = function (e) {
            e = e || window.event;
            if (e) {
                e.returnValue = 'Changes you made may not be saved.';
            }
            return 'Changes you made may not be saved.';
        }
        var id = document.getElementById('tmp_id').value;
        var formData = new FormData();
        formData.append('id',id);
        await axios.post('RequestPortal/backend/dlimg_checklist.php',formData)
        .then(
        (response)=>{
          this.imgbox = response.data;         
        }
      )    
      this.notshow=false;   
    },
    destroyed(){
      window.onbeforeunload=null;
    },
    methods:{
        handleUpdate(scope){
            var formData = new FormData();
            var id = document.getElementById('tmp_id').value;
            formData.append('id', id);
            formData.append('status', this.req.stat);
            formData.append('remarks', this.req.remarks);
            axios.post('RequestPortal/backend/update_check_record.php',formData);  
            this.$router.go(-1);        
        }
    }
}
</script>



